import apiService from './apiService';
import { setListPerson, updatePerson, updateImage } from 'store/reducers/personReducer';
import Profiles from '../views/utilities/Profiles';

const basePath = '/api/v1/person';

/**
 * Fetch to person data
 * @async
 * @function
 * @return {Promise<JSON>} Object campaign data
 * @param profile
 * @param page
 * @param pageSize
 * @param useCache
 */
export function fetchPersonList(profile = Profiles.Customer, page = 1, pageSize = 50, useCache = false) {
    return async function fetchPersonThunk(dispatch, getState) {
        let moreFilter = `&useCache=${useCache}`;
        moreFilter = moreFilter.concat(`&profileId=${profile}`);
        try {
            const url = `${basePath}/filter?pageSize=${pageSize}&page=${page}${moreFilter}`;
            const response = await apiService(getState()).get(url);
            await dispatch(setListPerson(response));
            return response;
        } catch (ex) {
            console.error('Error Fetch Person', ex.message);
            return null;
        }
    };
}

/**
 * Fetch person by code
 * @async
 * @function
 * @return {JSON} Object person data
 * @param code
 */
export function fetchPersonByCode(code) {
    return async function fetchPersonThunk(dispatch, getState) {
        try {
            const url = `${basePath}/CompleteData/${code}`;
            const response = await apiService(getState()).get(url);
            await dispatch(updatePerson(response));
            return response;
        } catch (ex) {
            console.error('Error Fetch Person', ex.message);
            return null;
        }
    };
}

export function updateDataPerson(data) {
    return async function updatePersonThunk(dispatch, getState) {
        try {
            const response = await apiService(getState()).post(basePath, data);
            if (response) {
                await dispatch(updatePerson(response));
                return true;
            }
            return false;
        } catch (ex) {
            console.error('Error Update Detail Person: ', ex.message);
            throw ex;
        }
    };
}

export function uploadImage(data) {
    return async function uploadImageThunk(dispatch, getState) {
        try {
            const url = `${basePath}/updateImage`;
            const response = await apiService(getState()).post(url, data);
            if (response) {
                const dataUpdate = { code: data.personCode, photoUrl: response };
                await dispatch(updateImage(dataUpdate));
                return true;
            }
            return false;
        } catch (ex) {
            console.error('Error Upload Image Person: ', ex.message);
            throw ex;
        }
    };
}
