import Card from '@mui/material/Card';
import Box from '@mui/material/Box';

const ShadowBox = ({ children, shadow }) => (
    <Card sx={{ boxShadow: shadow }}>
        <Box
            sx={{
                display: 'flex',
                bgcolor: 'primary.light',
                color: 'grey.800',
                marginLeft: 2
            }}
        >
            <Box sx={{ color: 'inherit' }}>{children}</Box>
        </Box>
    </Card>
);

export default ShadowBox;
