import { useEffect, useState } from 'react';
import { Formik } from 'formik';

import Divider from '@mui/material/Divider';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import MuiTypography from '@mui/material/Typography';

import SubCard from 'ui-component/cards/SubCard';
import ImagePanelUpload from 'ui-component/images/ImagePanelUpload';
import ShadowBox from 'ui-component/box/Shadow';
import AnimateButton from 'ui-component/extended/AnimateButton';

import InputMask from 'react-input-mask';
import PropTypes from 'prop-types';
import { setMessage } from 'store/reducers/dialogReducer';

import useScriptRef from 'hooks/useScriptRef';
import { useDispatch, useSelector } from 'react-redux';
import { uploadImage } from 'services/personService';
import Profiles, { hasPermissionManagerProfile, profileTranslate } from 'views/utilities/Profiles';
import _findIndex from 'lodash/findIndex';
import { loadAsyncImages } from 'views/utilities/ImageUtil';
import PersonPropTypes from 'views/utilities/custom-prop-types/PersonPropTypes';
import validateFields from 'views/utilities/Validates';

function Detail({ submitData, personCode, currentData, onChange, isSubmitting }) {
    const scriptedRef = useScriptRef();
    const dispatch = useDispatch();

    const profileIdsUser = useSelector((state) => state.user.person.profileIds);
    const [userProfiles, _] = useState(profileIdsUser);
    const [errors, setErrors] = useState();
    const [images, setImages] = useState([]);
    const [ckProfiles, setCkProfiles] = useState([]);

    function setImagesDownload(imageToSet) {
        setImages((oldValue) => oldValue.concat(imageToSet));
    }

    useEffect(() => {
        if (currentData != null) {
            const { multimedias } = currentData;
            loadAsyncImages(multimedias, setImagesDownload).catch();
        }
    }, [currentData, currentData.code]);

    useEffect(() => {
        if (personCode != null) {
            const personProfiles = (currentData || []).profileIds;
            // eslint-disable-next-line array-callback-return,no-unused-vars
            Object.entries(Profiles).map(([_, value]) => {
                const checked = (Array.isArray(personProfiles) && personProfiles.includes(value)) || value === Profiles.Customer;
                const disabled =
                    value === Profiles.Customer ||
                    (checked && (value === Profiles.Master || value === Profiles.Administrator) && userProfiles.includes(value));

                if (value === Profiles.Master && !userProfiles.includes(Profiles.Master)) {
                    return;
                }
                const data = { label: profileTranslate(value), value, checked, disabled };
                setCkProfiles((prevState) => [...prevState, data]);
            });
        }
        return () => setCkProfiles([]);
    }, [personCode, currentData.code]);

    function handleChange(event) {
        const index = _findIndex(ckProfiles, { value: parseInt(event.target.value, 10) });
        if (index >= 0) {
            ckProfiles[index].checked = event.target.checked;
            setCkProfiles(() => [...ckProfiles]);
        }
    }

    async function handleValidate() {
        const keysValidate = ['name', 'email', 'document', 'phone'];
        return dispatch(validateFields(keysValidate, currentData, (val) => setErrors(val)));
    }

    const handlePostData = async () => {
        let postData = currentData;
        const selectedProfiles = [];
        ckProfiles.filter((filter) => filter.checked).map((item) => selectedProfiles.push(item.value));
        postData = { ...postData, profileIds: selectedProfiles };
        submitData(postData);
    };

    async function uploadPersonImage(imageList) {
        try {
            if (personCode == null) {
                setImages(imageList);
                return;
            }
            const imagesPost = [];
            if ((imageList || []).length > 0) {
                imagesPost.push(imageList[0].dataUrl);
            }
            const postData = { personCode, images: imagesPost };
            await dispatch(uploadImage(postData));
            setImages(imageList);
        } catch (ex) {
            dispatch(setMessage({ message: `Erro: ${ex.message}` }));
            console.error('Error Post Data', ex.message);
        }
    }

    function renderImageUpload() {
        return (
            <ImagePanelUpload
                title="Atualizar Foto de Perfil"
                titleButton="Upload"
                images={images}
                onChange={(list) => uploadPersonImage(list)}
                maxNumber={1}
                multiple={false}
                renderAvatar
                altImage={currentData.name}
                positionButtonHeader={false}
            />
        );
    }

    function renderProfiles() {
        return (
            <Grid container>
                <Grid item xs={12} sm={12}>
                    <FormControl sx={{ mt: 3, ml: 3, mb: 3 }} component="fieldset">
                        <MuiTypography variant="h4" gutterBottom>
                            Permisses
                        </MuiTypography>
                        <FormGroup aria-label="position" row>
                            <Grid container spacing={2} direction="row" justifyContent="space-between">
                                {/* eslint-disable-next-line array-callback-return */}
                                {ckProfiles.map((item, index) => (
                                    <Grid item xs={12} sm={12} md={4} key={index} spacing={2}>
                                        <ShadowBox shadow="3">
                                            <FormControlLabel
                                                key={`ck_profile${index}`}
                                                control={
                                                    <Checkbox
                                                        checked={item.checked}
                                                        onChange={(event) => handleChange(event)}
                                                        value={item.value}
                                                        disabled={item.disabled}
                                                    />
                                                }
                                                label={item.label}
                                            />
                                        </ShadowBox>
                                    </Grid>
                                ))}
                            </Grid>
                        </FormGroup>
                    </FormControl>
                </Grid>
            </Grid>
        );
    }

    return (
        <Formik
            initialValues={{ currentData }}
            onSubmit={async (_values, { setStatus }) => {
                try {
                    if (scriptedRef.current) {
                        if (await handleValidate()) {
                            await handlePostData();
                            setStatus({ success: true });
                        }
                    }
                } catch (err) {
                    if (scriptedRef.current) {
                        setStatus({ success: false });
                        dispatch(setMessage({ message: err.message }));
                    }
                }
            }}
        >
            {({ handleSubmit }) => (
                <form noValidate autoComplete="off" onSubmit={handleSubmit}>
                    <Grid container spacing={{ sm: 0, md: 2 }}>
                        <Grid item xs={12} sm={3}>
                            <SubCard title="Foto de Perfil">{renderImageUpload()}</SubCard>
                        </Grid>
                        <Grid item xs={12} sm={9}>
                            <SubCard title="Detalhes da Conta">
                                <Grid container spacing={{ sm: 0, md: 2 }}>
                                    <Grid item xs={12} sm={12}>
                                        <TextField
                                            value={currentData?.name}
                                            fullWidth
                                            required
                                            error={Boolean(errors && errors.name)}
                                            name="name"
                                            label="Nome"
                                            onChange={(e) => onChange('name', e.target.value)}
                                            defaultValue=" "
                                        />
                                    </Grid>
                                    <Grid item xs={12} sm={7}>
                                        <TextField
                                            fullWidth
                                            label="Email"
                                            margin="normal"
                                            name="email"
                                            type="text"
                                            required
                                            error={Boolean(errors && errors.email)}
                                            value={currentData?.email}
                                            onChange={(e) => onChange('email', e.target.value)}
                                            defaultValue=" "
                                        />
                                    </Grid>
                                    <Grid item xs={12} sm={5}>
                                        <InputMask
                                            mask="999.999.999-99"
                                            value={currentData?.document}
                                            maskChar=" "
                                            onChange={(e) => onChange('document', e.target.value)}
                                            InputProps={{
                                                readOnly: Boolean(currentData.code != null)
                                            }}
                                            defaultValue=" "
                                        >
                                            {() => (
                                                <TextField
                                                    fullWidth
                                                    label="Documento"
                                                    margin="normal"
                                                    name="document"
                                                    defaultValue=" "
                                                    InputProps={{
                                                        readOnly: Boolean(currentData.code != null)
                                                    }}
                                                />
                                            )}
                                        </InputMask>
                                    </Grid>
                                    {/* ========================================= */}
                                    <Grid item xs={12} sm={4}>
                                        <InputMask
                                            mask="(99) 99999-9999"
                                            value={currentData?.phone}
                                            disabled={false}
                                            maskChar=" "
                                            onChange={(e) => onChange('phone', e.target.value)}
                                            defaultValue=" "
                                        >
                                            {() => <TextField fullWidth label="Celular" margin="normal" name="phone" />}
                                        </InputMask>
                                    </Grid>
                                    <Grid item xs={12} sm={4}>
                                        <InputMask
                                            mask="(99) 99999-9999"
                                            value={currentData?.whatsapp}
                                            disabled={false}
                                            maskChar=" "
                                            onChange={(e) => onChange('whatsapp', e.target.value)}
                                            defaultValue=" "
                                        >
                                            {() => (
                                                <TextField
                                                    fullWidth
                                                    label="WhatsApp"
                                                    margin="normal"
                                                    name="whatsapp"
                                                    value={currentData?.whatsapp}
                                                    defaultValue=" "
                                                />
                                            )}
                                        </InputMask>
                                    </Grid>
                                    <Grid item xs={12} sm={4}>
                                        <InputMask
                                            mask="(99) 99999-9999"
                                            value={currentData?.phoneSecondary}
                                            disabled={false}
                                            maskChar=" "
                                            onChange={(e) => onChange('phoneSecondary', e.target.value)}
                                            defaultValue=" "
                                        >
                                            {() => (
                                                <TextField
                                                    fullWidth
                                                    label="Recado"
                                                    margin="normal"
                                                    name="phoneSecondary"
                                                    value={currentData?.phoneSecondary}
                                                    defaultValue=" "
                                                />
                                            )}
                                        </InputMask>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Divider />
                                    </Grid>
                                    {hasPermissionManagerProfile(userProfiles) && renderProfiles()}
                                    <Grid item xs={12} sm={3}>
                                        <AnimateButton>
                                            <Button
                                                disableElevation
                                                disabled={isSubmitting}
                                                fullWidth
                                                size="small"
                                                type="submit"
                                                variant="contained"
                                                color="primary"
                                            >
                                                Salvar Detalhes
                                            </Button>
                                        </AnimateButton>
                                    </Grid>
                                </Grid>
                            </SubCard>
                        </Grid>
                    </Grid>
                </form>
            )}
        </Formik>
    );
}

Detail.propTypes = {
    onChange: PropTypes.func.isRequired,
    submitData: PropTypes.func.isRequired,
    personCode: PropTypes.string,
    isSubmitting: PropTypes.bool,
    currentData: PersonPropTypes
};

export default Detail;
