import { setMessage } from 'store/reducers/dialogReducer';

export default function validateFields(keysValidate, currentData, setSateErrors) {
    return async function validateFieldsThunk(dispatch, _) {
        setSateErrors(null);
        const error = {};
        let result = true;
        // eslint-disable-next-line array-callback-return
        keysValidate.map((key) => {
            if (['', null].includes(currentData[key])) {
                setSateErrors({ ...error[key] });
                result = false;
            }
        });
        if (!result) {
            dispatch(setMessage({ message: 'Os campos marcados com ( * ) so de preenchimento obrigatrios' }));
        }
        return result;
    };
}
