import PropTypes from 'prop-types';

const PersonPropTypes = PropTypes.shape({
    code: PropTypes.string,
    name: PropTypes.string,
    phone: PropTypes.string,
    phoneSecondary: PropTypes.string,
    whatsapp: PropTypes.string,
    email: PropTypes.string,
    document: PropTypes.string,
    profileIds: PropTypes.array,
    receiptEmail: PropTypes.bool,
    receiptNotification: PropTypes.bool,
    multimedia: PropTypes.arrayOf(
        PropTypes.shape({
            linkMedia: PropTypes.string
        })
    )
});

export default PersonPropTypes;
