import { useEffect, useState } from 'react';

import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import Stack from '@mui/material/Stack';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import { useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

// ==============================|| Services Import ||============================== //
import { fetchPersonByCode, updateDataPerson } from 'services/personService';
import MainCard from 'ui-component/cards/MainCard';
import Detail from './forms/Detail';
import Security from './forms/Security';
import Notification from './forms/Notification';
import { setMessage } from '../../../store/reducers/dialogReducer';

function Manager() {
    const dispatch = useDispatch();
    const location = useLocation();
    const person = useSelector((state) => state.person);
    const { items } = person;

    const [currentData, setCurrentData] = useState({ multimedias: [] });
    const [tab, setTab] = useState('1');
    const [personCode, setPersonCode] = useState(null);
    const [isSubmitting, setSubmitting] = useState(false);

    function handleChange(_event, newValue) {
        setTab(newValue);
    }

    async function fetchData() {
        const code = location.state?.code;
        if (code != null) {
            await dispatch(fetchPersonByCode(code));
            setPersonCode(code);
        }
    }

    useEffect(() => {
        fetchData().catch();
        return () => {
            setCurrentData({ multimedias: [] });
        };
    }, []);

    useEffect(() => {
        if (personCode != null) {
            const result = items.find((item) => item.code === personCode);
            if (result != null) {
                const multimedias = [{ linkMedia: result.photoUrl }];
                setCurrentData({ ...result, multimedias });
            }
        }
    }, [items]);

    function setValue(key, value) {
        setCurrentData({ ...currentData, [key]: value });
    }

    const submitData = async (postData) => {
        try {
            setSubmitting((prevState) => !prevState);
            await dispatch(updateDataPerson(postData));
            const action = [{ title: 'Ok', closeDialog: true }];
            dispatch(setMessage({ message: `Registro ${personCode ? 'editado' : 'inserido'} com sucesso!`, buttons: action }));
        } catch (ex) {
            dispatch(setMessage({ message: `Erro ao ${personCode ? 'editar' : 'inserir'} o registro` }));
            console.error('Error Post Data', ex.message);
        } finally {
            setSubmitting((prevState) => !prevState);
        }
    };

    function renderForm() {
        return (
            <>
                <TabPanel value="1">
                    <Stack>
                        <Detail
                            submitData={(data) => submitData(data)}
                            personCode={personCode}
                            currentData={currentData}
                            onChange={(key, value) => setValue(key, value)}
                            isSubmitting={isSubmitting}
                        />
                    </Stack>
                </TabPanel>
                <TabPanel value="4">
                    <Stack>
                        <Notification
                            submitData={(data) => submitData(data)}
                            currentData={currentData}
                            onChange={(key, value) => setValue(key, value)}
                            isSubmitting={isSubmitting}
                        />
                    </Stack>
                </TabPanel>
                <TabPanel value="5">
                    <Stack>
                        <Security currentData={currentData} onChange={(key, value) => setValue(key, value)} />
                    </Stack>
                </TabPanel>
            </>
        );
    }

    return (
        <MainCard title="Conta">
            <Box xs={12} sm={12} sx={{ bgcolor: 'white', borderColor: 'divider' }}>
                <Stack direction="row" justifyContent="center" alignItems="center" spacing={0}>
                    <span>Os campos marcados com ( * ) so de preenchimento obrigatrios</span>
                </Stack>
                <TabContext value={tab}>
                    <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                        <TabList
                            textColor="secondary"
                            indicatorColor="secondary"
                            onChange={(event, value) => handleChange(event, value)}
                            variant="scrollable"
                            scrollButtons="auto"
                            aria-label="Tabs Data"
                        >
                            <Tab label="Detalhes" value="1" />
                            <Tab label="Financeiro" value="2" />
                            <Tab label="Parceria" value="3" />
                            <Tab label="Notificaes" value="4" />
                            <Tab label="Seguraa" value="5" />
                        </TabList>
                    </Box>
                    {renderForm()}
                </TabContext>
            </Box>
        </MainCard>
    );
}

export default Manager;
