import { Grid, Switch, Typography } from '@mui/material';
import SubCard from 'ui-component/cards/SubCard';
import Box from '@mui/material/Box';
import MuiTypography from '@mui/material/Typography';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import PropTypes from 'prop-types';
import PersonPropTypes from '../../../utilities/custom-prop-types/PersonPropTypes';

function Notification({ submitData, currentData, onChange, isSubmitting }) {
    function changeSelection(value) {
        onChange(value.name, value.checked);
    }
    return (
        <Box noValidate autoComplete="off">
            <Grid container spacing={{ sm: 0, md: 2 }}>
                <Grid item xs={12} sm={6}>
                    <SubCard darkTitle title="Notificaes e Emails">
                        <Grid container sx={{ mb: 2 }} spacing={1} direction="column">
                            <Grid item>
                                <MuiTypography variant="h4" gutterBottom>
                                    Eu gostaria de receber
                                </MuiTypography>
                            </Grid>
                            <Grid item container xs={12} sm={5} sx={{ pt: 0 }} alignItems="center">
                                <Grid item sx={{ pt: 1 }} sm={4}>
                                    <Typography variant="subtitle1">Emails</Typography>
                                </Grid>
                                <Grid item>
                                    <Switch
                                        checked={currentData ? currentData.receiptEmail : false}
                                        onChange={(e) => changeSelection(e.target)}
                                        name="receiptEmail"
                                        size="small"
                                        inputProps={{ 'aria-label': 'controlled' }}
                                    />
                                </Grid>
                            </Grid>
                            <Grid item container xs={12} sm={5} alignItems="center">
                                <Grid item sm={4}>
                                    <Typography variant="subtitle1">Notificaes</Typography>
                                </Grid>
                                <Grid item>
                                    <Switch
                                        checked={currentData ? currentData.receiptNotification : false}
                                        onChange={(e) => changeSelection(e.target)}
                                        name="receiptNotification"
                                        size="small"
                                        inputProps={{ 'aria-label': 'controlled' }}
                                    />
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={12} sx={{ mb: 2 }}>
                            <Divider />
                        </Grid>
                        <Grid item xs={12} sm={3}>
                            <AnimateButton>
                                <Button
                                    onClick={() => submitData(currentData)}
                                    disabled={isSubmitting}
                                    disableElevation
                                    fullWidth
                                    size="small"
                                    variant="contained"
                                    color="primary"
                                >
                                    Salvar
                                </Button>
                            </AnimateButton>
                        </Grid>
                    </SubCard>
                </Grid>
            </Grid>
        </Box>
    );
}

Notification.propTypes = {
    submitData: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    isSubmitting: PropTypes.bool,
    currentData: PersonPropTypes
};

export default Notification;
