import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import SubCard from '../../../../ui-component/cards/SubCard';
import TextField from '@mui/material/TextField';
import { useTheme } from '@mui/material/styles';
import { Button, Typography, useMediaQuery } from '@mui/material';
import AnimateButton from 'ui-component/extended/AnimateButton';
import PropTypes from 'prop-types';
import PersonPropTypes from '../../../utilities/custom-prop-types/PersonPropTypes';

function Security({ currentData, errors, onChange }) {
    const theme = useTheme();
    const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));

    return (
        <Box noValidate autoComplete="off">
            <Grid container spacing={matchDownSM ? 0 : 2}>
                <Grid item xs={12} sm={9}>
                    <SubCard title="Alterar Senha">
                        <Grid container spacing={matchDownSM ? 0 : 2}>
                            <Grid item xs={12} sm={12}>
                                <TextField
                                    value={currentData?.currentPassword}
                                    fullWidth
                                    required
                                    error={Boolean(errors && errors.currentPassword)}
                                    name="currentPassword"
                                    label="Senha Atual"
                                    onChange={(e) => onChange('password', e.target.value)}
                                    defaultValue=" "
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    value={currentData?.password}
                                    fullWidth
                                    required
                                    error={Boolean(errors && errors.password)}
                                    name="password"
                                    label="Nova Senha"
                                    onChange={(e) => onChange('password', e.target.value)}
                                    defaultValue=" "
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    value={currentData?.newPassword}
                                    fullWidth
                                    required
                                    error={Boolean(errors && errors.newPassword)}
                                    name="newPassword"
                                    label="Confirme a nova senha"
                                    onChange={(e) => onChange('newPassword', e.target.value)}
                                    defaultValue=" "
                                />
                            </Grid>
                        </Grid>
                    </SubCard>
                </Grid>
                <Grid item xs={12} sm={3}>
                    <SubCard title="Desativar Conta">
                        <Grid container spacing={matchDownSM ? 0 : 2}>
                            <Grid item xs={12} sm={12}>
                                <Typography component="span" variant="h6">
                                    Ao selecionar est opo, o acesso aos recurso dos sistema seram desabilitados.
                                </Typography>
                            </Grid>
                            <Grid item xs={12} sm={10}>
                                <AnimateButton>
                                    <Button fullWidth disableElevation size="small" variant="outlined" color="error">
                                        Desativar Conta
                                    </Button>
                                </AnimateButton>
                            </Grid>
                        </Grid>
                    </SubCard>
                </Grid>
            </Grid>
        </Box>
    );
}

Security.defaultProps = {
    currentData: PersonPropTypes,
    currentPassword: PropTypes.string
};

export default Security;
